.navbar{
  border-bottom: 2px;
  border-bottom-color: #ff4c49;
  padding: 0.2em;
}
.nav-link, .item{
    font-family: "Futura", sans-serif;
    font-size: 20px;
    font-weight: bold;
    letter-spacing: 3px;
    padding: 15px;
  }

  .nav-item{
    width: 50%;
  }
  .navbar-nav{
    margin: auto;
    width: 50%;
    text-align: center;
  }

  .navbar-light .navbar-nav .nav-link:hover, .navbar-light .navbar-nav .nav-link:focus, .nav-item a.chosen,
  .nav-item a:hover {
    color: #ff4c49;
  }

  .card{
    background: #fff5f2;
    font-family: "Futura", sans-serif;
    color: #ff4c49;
    font-size: 20px;
    font-weight: bold;
    letter-spacing: 3px;
    padding: 70px;
    border: none;
  }

  h1{
    padding: 50px;
    font-weight: bold;
    text-align: center;
    letter-spacing: 4px;
  }

  /*Menu component*/
  button.item:focus, .item a.chosen,
  .item a:hover {
    border-bottom-width: 1px;
    border-bottom-color: #F14B31;
    border-bottom-style: solid;
    outline: none;
  }

  /*Graph components*/
  .graph {
    color: #575A89;
  }

  .ui.circular.button {
    border-radius: 16em;
  }

  i.icon, i.icons {
    font-size: 2em;
  }

  .ui.button{
    line-height: 2em;
  }

  .ui.card {
    width: 300px;
    height: 200px;
  }

  .ui.two.wide.grid.graph {
    margin-left: 2em;
  }

  /*Teaser Cards*/
  .teaserlink, .teaserlink.a:hover {
    color: #575A89;
    text-decoration: none;
    font-size: 10pt;
    padding-top: 5%;
  }

  .teaser-img {
    max-height: 150px;
    max-width: 150px;
    padding: 5%;
  }

  .intro-h1{
    color: #38BF3E;
    font-size: 70px;
    font-weight: 900;
  }

  .intro-h2{
    font-size: 40px;
    color: #575A89;
    text-align: center;
    margin: auto;
    width: 70%;
  }

  .intro-h3{
    font-size: 30px;
    color: #575A89;
    text-align: center;
    padding: 20px;
    margin: auto;
    width: 85%;
  }

  .intro-h4{
      font-size: 50px;
      color: #575A89;
      width: 70%;
      font-weight: 900;
      margin-left: 200px;
      margin-bottom: 30px;
      margin-top: 50px;
  }

  .intro-p{
    font-size: 25px;
    color: #575A89;
    margin-left: 200px;
    width: 80%;
    font-weight: 600;
  }

  .teaser{
    background-color:#fff5f2;
    padding: 30px;
  }

  .five.wide.column{
    font-size: 40px;
    font-weight: bold;
    color: #575A89;
    margin: auto;
    background-color: antiquewhite;
    text-align: center;
    letter-spacing: 6px;
    
    
  }

  .article-card{
    font-size: 10px;
    padding: 50px;
  }

  .article-text{
    font-size: 25px;
    background-color: antiquewhite;
    color: #575A89;
    font-weight: 500;
    letter-spacing: 0px;
  }

  .article-pic{
    padding: 10px;
    width: 200px;
  }

  .first-column{
    background-color: antiquewhite;
  }
  /*got it from https://stackoverflow.com/questions/3790935/can-i-hide-the-html5-number-input-s-spin-box*/
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    /* display: none; <- Crashes Chrome on hover */
    -webkit-appearance: none;
    margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}

input[type=number] {
    -moz-appearance:textfield; /* Firefox */
}

.label {
    padding: 20px;
    font-weight: bold;
    text-align: center;
}